var that;
export default {
	data() {
		return {
			TabCur: -1,
			scrollLeft: 0,
			navtitle: [
				{ id: -1, name: '全部' },
				{ id: 0, name: '待付款' },
				{ id: 1, name: '待发货' },
				{ id: 2, name: '待收货' },
				{ id: 3, name: '已完成' },
				{ id: 4, name: '售后' }
			],
			list: [],
			onlist: false,
			page: 1,
			onmore: false,
			access_token: '',
			defaultData: {},
			showModel: false,
			deleteId: '',
			deleteType:'',
			deleteIndex:''
		};
	},
	methods: {
		login() {
			that.$zax.yzlogin();
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;
			that.page = 1;
			that.list = [];
			if (that.access_token) {
				that.getorder();
			}
		},
		// 售后列表
		shouhuolist(e) {
			this.TabCur = e.currentTarget.dataset.index;
			that.list = [];
			this.$request.request({
				url: that.api.order.list,
				data: {
					status: that.TabCur,
					order_id: e.currentTarget.dataset.id
				},
				Toast: '正在加载...',
				success(res) {
					if (res.list.length > 0) {
						that.list = that.list.concat(res.list);
					} else {
						if (that.page != 1) {
							if (!that.nomore) {
								that.$alert('没有更多了！');
								that.nomore = true;
							}
						} else {
							that.onlist = false;
						}
					}
				}
			});
		},
		totclist(e) {
			uni.navigateTo({
				url: '/pages/card/tcinfo/tcinfo?id=' + e.currentTarget.dataset.id
			});
		},
		todzlist(e) {
			uni.navigateTo({
				url: '/pages/card/cardinfo/cardinfo?id=' + e.currentTarget.dataset.id
			});
		},
		getorder() {
			that.onlist = true;
			that.$request.request({
				url: that.api.order.list,
				data: {
					status: that.TabCur,
					page: that.page
				},
				success(res) {
					console.log(res);
					that.page == 1 ? (that.list = []) : '';
					if (res.list.length > 0) {
						let list = res.list;
						that.list = that.list.concat(list);
					} else {
						if (that.page != 1) {
							if (!that.nomore) {
								that.$alert('没有更多了！');
								that.nomore = true;
							}
						} else {
							that.onlist = false;
						}
						that.list = [];
					}
				}
			});
		},
		tpinfo(e) {
			uni.navigateTo({
				url: '/pages/order/shopOrderinfo/shopOrderinfo?id=' + e.currentTarget.dataset.id
			});
		},
		//评价
		pingjia(e) {
			uni.navigateTo({
				url: '/pages/order/pingjia/pingjia?id=' + e.currentTarget.dataset.id
			});
		},
		// 第二种详情
		tpinfo2(e) {
			// 售后详情
			console.log(e.currentTarget.dataset.order_refund_id);
			if (e.currentTarget.dataset.order_refund_id) {
				uni.navigateTo({
					url: '/pages/order/shouhou/shouhouInfo/shouhouInfo?id=' + e.currentTarget.dataset.order_refund_id
				});
			} else {
				uni.navigateTo({
					url: '/pages/order/orderinfo2/orderinfo2?id=' + e.currentTarget.dataset.id  
				});
			}
		},
		sModel(e) {
			let detail = e.currentTarget.dataset;
			that.deleteId=detail.id; 
			that.deleteIndex=detail.index;
			that.showModel = true;
			that.defaultData = { 
				content: '是否取消该订单',
				cancelText: '取消',
				confirmText: '确认',
				confirmColor: '#4c4c4c'
			};
		},
		onConfirm(){
			that.$request.request({
				url: that.api.order.revoke,
				data: {
					order_id:that.deleteId 
				},
				Toast: '正在执行...',
				success(res) {
					let orderinfo=that.list[that.deleteIndex]; 
					if(orderinfo.is_pay){ 
						orderinfo.apply_delete=1
						that.list[that.deleteIndex]=orderinfo
					}else{
						that.list.splice(that.deleteIndex, 1); 
					} 
					that.$alertok("操作成功！") 
				},
				complete(){
					uni.hideLoading()
				}
			});
		},
		//取消订单
		orderRevoke: function(e) {
			let detail = e.currentTarget.dataset,
				index = detail.index,
				is_pay = detail.play; 
			console.log('支付', detail);
			uni.showModal({
				title: '提示',
				content: '是否取消该订单',
				cancelText: '否',
				confirmText: '是',
				success: function(t) {
					if (t.cancel) return !0;
					if (t.confirm) {
						that.$request.request({
							url: that.api.order.revoke,
							data: {
								order_id: e.currentTarget.dataset.id,
								o_type: order_type
							},
							Toast: '正在执行...',
							success(res) {
								// that.list.splice(index, 1);
								// that.$showModal(is_pay == 1 ? '订单取消申请已提交，请等候管理员审核' : '订单已取消');
								let orderinfo=that.list[index];
								if(orderinfo.is_pay){ 
									orderinfo.apply_delete=1
									that.list[index]=orderinfo
								}else{
									that.list.splice(index, 1); 
								} 
								that.$alertok("操作成功！")  
							}
						});
					}
				}
			});
		},
		// 确认收货
		orderConfirm: function(e) { 
			uni.showModal({
				title: '提示',
				content: '是否确认已收到货？',
				cancelText: '否',
				confirmText: '是',
				success: function(t) {
					if (t.cancel) return !0;
					that.$request.request({
						url: that.api.order.confirm,
						data: {
							order_id: e.currentTarget.dataset.id, 
						},
						Toast: '操作中...',
						success(res) {
							that.$alertok('已确认收货');
							setTimeout(function() {
								that.getorder();
							}, 1000);
						}
					});
				}
			});
		},
		pay(e) {
			let detail = e.currentTarget.dataset;
			console.log(detail);
			let index = detail.index;
			let id = detail.id;  
			that.$pay
				.pay(0, {
					order_id: id,
					order_type: order_type
				})
				.then(res => {
					console.log(res);
					that.list[index].is_pay = 1;
				});
			 
		}
	},
	onLoad(option) {
		that = this;
		that.TabCur = option.id ? option.id : 1;
		that.scrollLeft = (option.id - 1) * 60;
	},
	onShow() {
		that.access_token = uni.getStorageSync('access_token');
		if (that.access_token) {
			that.getorder();
		}
	},
	onPullDownRefresh() {
		that.getorder();
	}
};
